@import './variables.scss';

@mixin h1 {
  font-size: $sizeLg1;
}
@mixin h2 {
  font-size: $sizeMd3;
}
@mixin h3 {
  font-size: $sizeMd2;
}
@mixin h4 {
  font-size: $sizeMd1;
}

@mixin --viewportSm {
  @media (min-width: $breakpointSm) {
    @content;
  }
}

@mixin --viewportMd {
  @media (min-width: $breakpointMd) {
    @content;
  }
}

@mixin --viewportLg {
  @media (min-width: $breakpointLg) {
    @content;
  }
}
